<template>
    <v-app class="app">
        <v-main>
            <v-container>
                <v-row>
                    <v-col cols="12" md="3" class="d-none d-md-block">

                        <v-card color="transparent" flat>
                            <v-card-text>
                                <v-row dense>
                                    <v-col cols="12">
                                        <div class="font-weight-medium primary--text ">
                                            Skills
                                        </div>
                                    </v-col>

                                    <template v-for="(skill, index) in skills">
                                        <v-col cols="6" :key="index">
                                            <skill-card :text="skill.text" :icon="skill.icon"></skill-card>
                                        </v-col>
                                    </template>
                                </v-row>
                            </v-card-text>
                        </v-card>
                    </v-col>
                    <v-col cols="12" md="9">
                        <v-breadcrumbs :items="$route.meta.breadcrumbs" class="pl-0 mb-2" divider=">">
                            <template v-slot:item="{ item }">
                                <v-icon color="primary" class="mr-1">{{
                                    item.icon
                                }}</v-icon><span class="font-weight-medium subtitle-2 primary--text">{{ item.text
                                    }}</span>
                            </template>
                            <template v-slot:divider>
                                <v-icon>mdi-chevron-right</v-icon>
                            </template>
                        </v-breadcrumbs>
                        <v-card rounded flat elevation="1">
                            <v-row>
                                <v-col cols="12" md="7">
                                    <v-list-item two-line>
                                        <v-list-item-avatar :size="150">
                                            <v-img :src="require('@/assets/myavatar.png')
                                                "></v-img>
                                        </v-list-item-avatar>
                                        <v-list-item-content>
                                            <v-list-item-title class="font-weight-bold" style="font-size: 1.3rem">Hi,
                                                I'm Ragie Doromal 👋</v-list-item-title>
                                            <v-list-item-subtitle>Web
                                                Developer | Graphic Artist | Teacher</v-list-item-subtitle>
                                        </v-list-item-content>
                                    </v-list-item>

                                    <v-card-text>
                                        <div class="text-justify black--text mb-5">
                                            {{ displayedText }}
                                        </div>

                                        <div class="d-block d-md-none">
                                            <v-row dense>
                                                <template v-for="(
                                                        skill, index
                                                    ) in skills">
                                                    <v-col cols="6" sm="4" :key="index">
                                                        <skill-card :text="skill.text" :icon="skill.icon"></skill-card>
                                                    </v-col>
                                                </template>
                                            </v-row>
                                        </div>

                                        <div class="d-block d-md-none">
                                            <v-list>
                                                <v-list-item class="pl-0">
                                                    <v-list-item-content>
                                                        <v-list-item-subtitle>Resume</v-list-item-subtitle>
                                                        <v-list-item-title class="text-decoration-underline"><v-btn text
                                                                href="./RAGIEDOROMAL_RESUME.pdf" target="_blank"
                                                                class="pl-0">
                                                                RAGIEDOROMAL_RESUME.pdf
                                                            </v-btn></v-list-item-title>
                                                    </v-list-item-content>
                                                </v-list-item>
                                                <v-list-item class="pl-0">
                                                    <v-list-item-content>
                                                        <v-list-item-subtitle>Email Address</v-list-item-subtitle>
                                                        <v-list-item-title>
                                                            {{ email }}
                                                        </v-list-item-title>
                                                    </v-list-item-content>
                                                </v-list-item>
                                                <v-list-item class="pl-0">
                                                    <v-list-item-content>
                                                        <v-list-item-subtitle>Mobile
                                                            Number</v-list-item-subtitle>
                                                        <v-list-item-title>
                                                            {{ mobileNumber }}
                                                        </v-list-item-title>
                                                    </v-list-item-content>
                                                </v-list-item>
                                                <v-list-item class="pl-0">
                                                    <v-list-item-content>
                                                        <v-list-item-subtitle>Socials</v-list-item-subtitle>
                                                        <v-list-item-title>
                                                            <template v-for="(
                                                                    social,
                                                                        index
                                                                ) in socials">
                                                                <v-btn depressed target="_blank" :href="social.link
                                                                    " class="mr-1" :key="index">
                                                                    <v-icon class="mr-1">{{
                                                                        social.icon
                                                                        }}</v-icon>
                                                                    <span class="text-capitalize">
                                                                        {{
                                                                            social.text
                                                                        }}
                                                                    </span>
                                                                </v-btn>
                                                            </template>
                                                        </v-list-item-title>
                                                    </v-list-item-content>
                                                </v-list-item>
                                            </v-list>
                                        </div>
                                    </v-card-text>
                                </v-col>
                                <v-col cols="12" md="5" class="d-none d-md-block mb-md-5">
                                    <v-list dense>
                                        <v-list-item>
                                            <v-list-item-content>
                                                <v-list-item-subtitle>Resume</v-list-item-subtitle>
                                                <v-list-item-title class="text-decoration-underline"><v-btn small text
                                                        href="./RAGIEDOROMAL_RESUME.pdf" target="_blank" class="pl-0">
                                                        RAGIEDOROMAL_RESUME.pdf
                                                    </v-btn></v-list-item-title>
                                            </v-list-item-content>
                                        </v-list-item>
                                        <v-list-item>
                                            <v-list-item-content>
                                                <v-list-item-subtitle>Email Address</v-list-item-subtitle>
                                                <v-list-item-title>
                                                    {{ email }}
                                                </v-list-item-title>
                                            </v-list-item-content>
                                        </v-list-item>
                                        <v-list-item>
                                            <v-list-item-content>
                                                <v-list-item-subtitle>Mobile
                                                    Number</v-list-item-subtitle>
                                                <v-list-item-title>
                                                    {{ mobileNumber }}
                                                </v-list-item-title>
                                            </v-list-item-content>
                                        </v-list-item>
                                        <v-list-item>
                                            <v-list-item-content>
                                                <v-list-item-subtitle>Socials</v-list-item-subtitle>
                                                <v-list-item-title>
                                                    <template v-for="(
                                                            social, index
                                                        ) in socials">
                                                        <v-btn depressed small target="_blank" :href="social.link"
                                                            class="mr-1" :key="index">
                                                            <v-icon small class="mr-1">{{
                                                                social.icon
                                                                }}</v-icon>
                                                            <span class="text-capitalize">
                                                                {{
                                                                    social.text
                                                                }}
                                                            </span>
                                                        </v-btn>
                                                    </template>
                                                </v-list-item-title>
                                            </v-list-item-content>
                                        </v-list-item>
                                    </v-list>
                                </v-col>
                            </v-row>
                            <v-tabs color="info" v-model="tab" hide-slider>
                                <template v-for="(tab, index) in tabs">
                                    <v-tab class="text-capitalize subtitle-2" exact exact-active-class="info--text"
                                        :to="tab.to" :key="index">{{ tab.text }}</v-tab>
                                </template>
                            </v-tabs>

                            <v-card-text>
                                <v-tabs-items v-model="tab">
                                    <router-view></router-view>
                                </v-tabs-items>
                            </v-card-text>
                        </v-card>
                    </v-col>
                </v-row>
            </v-container>
        </v-main>
    </v-app>
</template>

<script>
import SkillCard from '@/components/custom/SkillCard';
import informationMixin from '@/mixins/information';

export default {
    name: 'home-layout',

    mixins: [informationMixin],

    components: { SkillCard },

    data() {
        return {
            typingText: "A versatile professional with expertise across web development, teaching, and graphic artistry. I bring a unique blend of creativity, mentorship, and technical skill to every task. Driven by a passion for continuous learning, I am always eager to tackle new challenges and contribute meaningful value to a collaborative team.",
            displayedText: "",
            typingIndex: 0,
            tab: null,
            tabs: [
                {
                    text: 'Experience',
                    to: { name: 'home' },
                },
                { text: 'Projects', to: { name: 'project' } },
            ],
            navigations: [
                {
                    text: 'Home',
                    to: { name: 'home' },

                },
                {
                    text: 'Projects',
                    to: { name: 'project' },

                },
            ],
        };
    },
    mounted() {
        this.typeEffect();
    },
    methods: {
        typeEffect() {
            if (this.typingIndex < this.typingText.length) {
                this.displayedText += this.typingText[this.typingIndex];
                this.typingIndex++;
                setTimeout(this.typeEffect, 30);
            }
        }
    }
};
</script>

<style lang="scss" scoped>
.app {
    background-color: #fcf7f6 !important;
}
</style>
